._container{
   max-width: 500px;
   margin: 0 auto;
}
.wrapper{
   /* background:black; */
   margin-top: 10px;
}
.header{
   position: relative;
   display: flex;
   justify-content: center;
   align-items: center;
   /* padding-left: 30px; */
   /* height: 100px; */
   background: rgb(102, 151, 2);
}


.add_task{
   display: flex;
   flex-direction: column;
   background-color: rgb(44, 136, 44);
   
   align-items: center;
}
.add_task_text{
   color: white;
}
.add_task_input_and_button{
 margin-top: 20px;
 margin-bottom: 20px;
}
.add_task_input{
   text-align: center;
   background-color: #d1db3f;
   border:none;
   font-size: 15px;
   padding-top: 10px;
   padding-bottom: 10px;
}
.button_add{
  padding-top: 10px;
  padding-bottom: 10px;
   background-color: #db973f;
}
.button_add_text{
   font-size: 15px;
}
.add_task_result{
   background-color: aliceblue;
   display: flex;
   justify-content: space-between;
   padding-top: 10px;
   padding-bottom: 20px;
   /* overflow: hidden; */
}
.add_task_result_text{
   width: 50%;
   word-wrap: break-word;
   font-size: 18px;
   color:black;
   margin-left: 20px;

}
.add_task_result_text1{
   text-decoration: line-through;
}
.add_task_result_text_remove{
   margin-right: 20px;
}
.add_task_input1{
   background: rgb(253, 188, 186);
}
.add_task_input2{
   display: none;
}
.filter_block{
   background: #d1db3f;
   display: flex;
   justify-content: center;
   padding-top: 25px;
   padding-bottom: 10px;
}
.filter{
/* width: 100%; */
background-color: #db973f;
/* text-align: center; */
font-size: 18px;
}

.header_wrapper{
   display: flex;
   flex-direction: column;
   justify-content: center;

}

.header_span{
text-align: center;
color:white;
font-size: 25px;
font-weight: bold;
padding-top: 10px;
padding-bottom: 10px;
}
.header_span2{
   font-size: 30px;
   color: white;
   /* padding-left: 20px; */
   text-align: center;
   padding-bottom: 10px;
}

@media(max-width:415px) {
   .add_task_input_and_button{
      justify-content: center;
      align-items:center;
      text-align: center;
   }
   .button_add {
      padding-top: 10px;
      padding-bottom: 10px;
      margin-top: 20px;
      margin-bottom: 20px;
   }
   .filter_block{
      justify-content: center;
   }
}